import React, {useState, useEffect, useCallback, useRef} from 'react';
import TypingAnimation from '../components/TypingAnimation';
import SEO from '../components/SEO';
import cvPdf from '../public-cv.pdf';
import {MdEmail} from 'react-icons/md';
import {FaArrowDown, FaGithub, FaLinkedin} from 'react-icons/fa';
import {IoIosArrowDown} from 'react-icons/io';
import {navigate} from 'gatsby';

import "../styles/main.scss";

const idleTime = 700; // Pauses between typed messages
const messages = [
  'Hi!!!',
  "Hi... I'm Joshua.",
  "Hi... I'm Joshua!",
];

const linesToScroll = 10;
const scrollTimeout = 100;

export default () => {
  const [messageIndex, setMessageIndex] = useState(0);
  const [greetingFinished, setGreetingFinished] = useState(false);
  const nextMessage = useCallback(()=>{
    if(messageIndex>=messages.length-1){
      setTimeout(()=>setGreetingFinished(true), idleTime);
    }
    else{
      setTimeout(
        ()=>setMessageIndex(i=>i<messages.length-1?i+1:i),
        idleTime
      );
    }
  }, [messageIndex]);

  const [scrolled, setScrolled] = useState(0);
  const onWheel = useCallback(e=>{
    const dY = e.deltaY;
    setScrolled(s=>s+dY);
  }, []);
  const [leaving, setLeaving] = useState(false);
  const nextPage = useCallback(()=>{
    setLeaving(true);
    setTimeout(()=>navigate('/hello'), 2500);
  }, []);
  useEffect(()=>{
    if(scrolled >= linesToScroll){
      nextPage();
    }
    const id = setTimeout(()=>setScrolled(0), scrollTimeout);
    return ()=>clearTimeout(id);
  }, [scrolled]);
  return (
    <>
      <SEO title="Software Developer"/>
      <main className={leaving?'leaving':''} onTouchEnd={(e)=>{console.log('wat', e.changedTouches)} } onWheel={onWheel}>
        <h1 className="header-message">
          <TypingAnimation
            message={messages[messageIndex]}
            onMessageTyped={nextMessage}
          />
        </h1>
        <div className={`links-bar ${greetingFinished?'show':''}`}>
          <a href="https://github.com/mynjj">
            <div className="link-icon">
              <FaGithub/>
            </div>
          </a>
          <a href="https://www.linkedin.com/in/joshua-mart%C3%ADnez-941668145/">
            <div className="link-icon">
              <FaLinkedin/>
            </div>
          </a>
          <a href={cvPdf}>
            <div className="text-icon">
              CV
            </div>
          </a>
          <a href="mailto:djoshuamartinezpineda@gmail.com">
            <div className="link-icon">
              <MdEmail/>
            </div>
          </a>
        </div>
      </main>
      <div className={`scroll-more-bar ${greetingFinished?'glow':''}`}>
        <IoIosArrowDown/>
      </div>
      <div className={`next-page ${leaving?'grow':''}`}/>
    </>
  );
}
